-ms-transition: all 0.5s ease;\r\n  -o-transition: all 0.5s ease;\r\n}\r\n\r\n.menu-toggle {\r\n  display: block;\r\n}\r\n\r\n.menu-toggle .bar {\r\n  width: 15px;\r\n  height: 2px;\r\n  margin: 3px auto;\r\n  transition: all 0.3 ease-in-out;\r\n  background-color: var(--whiteColor);\r\n  cursor: pointer;\r\n  -webkit-transition: all 0.3 ease-in-out;\r\n  -moz-transition: all 0.3 ease-in-out;\r\n  -ms-transition: all 0.3 ease-in-out;\r\n  -o-transition: all 0.3 ease-in-out;\r\n}\r\n\r\nheader {\r\n  min-height: 500px;\r\n  position: relative;\r\n  background-color: #3377ff;\r\n  width: 100%;\r\n  background-position: center;\r\n  background-repeat: no-repeat;\r\n  background-size: cover;\r\n}\r\n\r\n.header-title {\r\n  position: absolute;\r\n  top: 35%;\r\n  left: 10%;\r\n  font-weight: bolder;\r\n  font-size: 35px;\r\n  color: var(--whiteColor);\r\n  text-shadow: 3px 2px 0px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.header-title > span {\r\n  color: var(--labelColor);\r\n}\r\n\r\n.header-subtitle {\r\n  position: absolute;\r\n  top: 65%;\r\n  left: 13%;\r\n  font-weight: bolder;\r\n  font-size: 18px;\r\n  color: var(--whiteColor);\r\n  text-shadow: 3px 2px 0px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.search-box {\r\n  position: absolute;\r\n  display: flex;\r\n  align-items: center;\r\n  min-width: 400px;\r\n  margin-top: 10px;\r\n  top: 70%;\r\n  left: 13%;\r\n}\r\n\r\n.search-box input[type='text'] {\r\n  width: 100%;\r\n  padding: 8px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 4px;\r\n  margin-right: 4px;\r\n}\r\n\r\n.search-box button {\r\n  padding: 8px 12px;\r\n  background-color: var(--labelColor);\r\n  border: none;\r\n  color: var(--greyColor);\r\n  border-radius: 4px;\r\n  cursor: pointer;\r\n}\r\n\r\n.search-box button:hover {\r\n  border: 2px solid var(--labelColor);\r\n  color: var(--labelColor);\r\n  background-color: var(--primaryColor);\r\n}\r\n\r\n.image-header {\r\n  position: absolute;\r\n  width: 450px;\r\n  top: 10%;\r\n  right: 4%;\r\n  background-image: none;\r\n  /* transform: scaleX(-1); */\r\n}\r\n\r\n/* Like */\r\n\r\n.save {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n/* Home Page */\r\n.job-preview-content img {\r\n  max-height: fit-content;\r\n  max-width: 50vw;\r\n}\r\n\r\n.blog {\r\n  width: 100%;\r\n  height: auto;\r\n  text-align: center;\r\n  padding: 5rem 1rem;\r\n}\r\n\r\n.blog-wrapper {\r\n  display: flex;\r\n  justify-content: center;\r\n  width: 100%;\r\n  padding: 1rem;\r\n}\r\n\r\n.blog-card {\r\n  width: 300px;\r\n  height: auto;\r\n  margin: 1rem;\r\n  box-shadow: 0 1px 6px 0 rgba(49 53 59 / 12%);\r\n  cursor: pointer;\r\n}\r\n.blog-card:hover {\r\n  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0;\r\n}\r\n\r\n.blog-image {\r\n  width: 100%;\r\n  height: 150px;\r\n  object-fit: cover;\r\n}\r\n\r\n.blog-detail {\r\n  padding: 20px;\r\n}\r\n\r\n.blog-detail span {\r\n  color: var(--primaryColor);\r\n  font-size: 0.7rem;\r\n}\r\n\r\n.blog-detail h4 {\r\n  padding: 10px 0;\r\n}\r\n\r\n.divinder {\r\n  margin: 1rem 0;\r\n  border: 0.5px solid var(--primaryColor);\r\n}\r\n\r\n.blog-more {\r\n  color: var(--primaryColor);\r\n  text-decoration: none;\r\n  font-size: 16px;\r\n}\r\n\r\ni {\r\n  color: var(--whiteColor);\r\n}\r\n\r\n/* About Us Page */\r\n.about-title {\r\n  background-color: #3377ff;\r\n  border-bottom-left-radius: 20px;\r\n  border-bottom-right-radius: 20px;\r\n  position: relative;\r\n  width: 100%;\r\n  background-size: cover;\r\n  font-weight: bolder;\r\n  font-size: 35px;\r\n  color: var(--whiteColor);\r\n  text-shadow: 3px 2px 0px rgba(0, 0, 0, 0.2);\r\n  padding-bottom: 30px;\r\n}\r\n.about-title > h2 {\r\n  left: 2%;\r\n  position: absolute;\r\n  text-decoration: underline;\r\n}\r\n.about-title